async function fetchDashboardAnalyticFactory({commit}, payload) {
    return await this.$axios
        .get('/dashboard-analytic-factory', payload)
        .then((res) => {
            if (res && res.data) {
                commit("SET_DASHBOARD_ANALYTIC_FACTORY", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}







export default {
    fetchDashboardAnalyticFactory
   
};
